.button {
  color: $accent-light;
  cursor: pointer;
  margin-right: 20px;
  height: 45px;
  line-height: 45px;
  padding: 0 25px;
  text-align: center;
  border: none;
  background-size: 300% 100%;
  text-transform: uppercase;
  border-radius: 45px;
  moz-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  text-decoration: none;
  display: inline-block;
  background-image: linear-gradient(
    to right,
    $color-accent1,
    $color-accent2,
    $color-accent1,
    $color-accent2
  );
  box-shadow: 0 4px 15px 0 $color-base2;

  &:last-of-type {
    margin-right: 0;
  }

  @include md {
    padding: 0 45px;
    margin-right: 30px;
  }
}

.button:hover {
  background-position: 100% 0;
  moz-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}

.button:active {
  background-image: linear-gradient(
    to right,
    $color-base1,
    $color-base2,
    $color-base1,
    $color-base2
  );
  box-shadow: inset 0 1px 5px $accent-dark;
}
.button.active {
  color: $color-base1;
  background-image: linear-gradient(
    to right,
    $color-contrast1,
    $color-contrast2,
    $color-contrast1,
    $color-contrast2
  );
  box-shadow: inset 0 1px 5px $accent-dark;
}

.button:disabled {
  display: none;
}
